import axios from 'axios';
import { createAsyncThunk } from '@reduxjs/toolkit';
axios.defaults.baseURL = 'https://connections-api.herokuapp.com';
const token = {
  set(token) {
    axios.defaults.headers.common.Authorization = `Bearer ${token}`;
  },
  unset() {
    axios.defaults.headers.common.Authorization = '';
  },
};
const register = createAsyncThunk('auth/register', async userRegInfo => {
  try {
    const { data } = await axios.post('/users/signup', userRegInfo);
    token.set(data.token);
    return data;
  } catch (error) {
    console.error();
  }
});
const logIn = createAsyncThunk('auth/login', async userInfo => {
  try {
    const { data } = await axios.post('/users/login', userInfo);
    token.set(data.token);
    return data;
  } catch (error) {
    console.error();
  }
});
const logOut = createAsyncThunk('auth/logout', async () => {
  try {
    await axios.post('/users/logout');
    token.unset();
  } catch (error) {
    console.error();
  }
});
const fetchCurrentUser = createAsyncThunk(
  'auth/refresh',
  async (_, thunkAPI) => {
    const state = thunkAPI.getState();
    const persistedToken = state.auth.token;

    if (persistedToken === null) {
      console.log(' ');
      return thunkAPI.rejectWithValue();
    }

    token.set(persistedToken);
    try {
      const { data } = await axios.get('/users/current');
      return data;
    } catch (error) {
      console.error();
    }
  }
);
const operation = { register, logIn, logOut, fetchCurrentUser };
export default operation;
